@-webkit-keyframes overlay-slide-from-top {
  0% {
    -webkit-transform: translateY(-2.5rem);
            transform: translateY(-2.5rem);
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes overlay-slide-from-top {
  0% {
    -webkit-transform: translateY(-2.5rem);
            transform: translateY(-2.5rem);
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.overlay-backdrop {
  z-index: 10;
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: var(--color-accent-200-4);
}
.overlay-panel {
  -webkit-animation: overlay-slide-from-top 0.5s;
          animation: overlay-slide-from-top 0.5s;
  transition: 0.3s;
  background-color: #fffffff0;
  max-height: 75vh;
  overflow-y: scroll;
  margin: 2vh auto 0 auto;
  padding: 1rem 2.5ch;
  box-shadow: 1px 1px 3px #666;
  width: min(65ch, 92vw);
}


@-webkit-keyframes action-button-slide-up {
  0% {
    -webkit-transform: translateY(2.5rem);
            transform: translateY(2.5rem);
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


@keyframes action-button-slide-up {
  0% {
    -webkit-transform: translateY(2.5rem);
            transform: translateY(2.5rem);
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.action-button {
  --position: 0;
  --bkg: var(--color-secondary-600);
  -webkit-animation: action-button-slide-up 0.5s;
          animation: action-button-slide-up 0.5s;
  transition: 0.5s;
  z-index: 5;
  position: fixed;
  right: calc(var(--position) * 3.5rem + 1rem);
  bottom: 1.5rem;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 1.25rem;
  border: 1px solid black;
  background-color: var(--bkg);
  box-shadow: 1px 1px 3px #666;
  cursor: pointer;
}
.action-button img {
  width: 100%;
}
@media (hover: hover) {
  .action-button:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 2px 2px 7px gray;
  }
}
.list-view-button {
  --position: 1;
}
.pin-list-view header {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1.5rem 1fr 1.5rem;
}
.pin-list-view ul {
  list-style-type: none;
}
.pin-list-view li {
  display: grid;
  grid-template-columns: 2ch 1fr;
}
html, body {
  position: absolute;
  padding: 0;
  margin: 0;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-accent-500);
  background: linear-gradient(45deg, var(--color-accent-500) 0%, var(--color-primary-300-4) 45%, var(--color-primary-400) 100%);
}
* {
  font-family: var(--primary-font-family);
  letter-spacing: var(--letter-spacing);
  box-sizing: border-box;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
input {
  -webkit-user-select: text;
      -ms-user-select: text;
          user-select: text;
}
img {
  max-width: 100%;
}
@-webkit-keyframes drop-pin-rise {
  0% {
    bottom: 0;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes drop-pin-rise {
  0% {
    bottom: 0;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
button.drop-pin-button {
  --bkg: var(--color-primary-600);
}
.drop-pin-button img {
  width: 100%;
}
.fire-button {
  -webkit-animation: drop-pin-rise 0.5s;
          animation: drop-pin-rise 0.5s;
  z-index: 10;
  cursor: pointer;
  position: fixed;
  right: 1rem;
  bottom: 1.5rem;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 1.25rem;
  border: 1px solid var(--color-primary-200);
  background-color: var(--color-secondary-600);
  box-shadow: 1px 1px 3px #666;
  transition: 0.3s;
  display: grid;
  align-items: center;
  justify-items: center;
}
.fire-button img {
  width: 1.5rem;
}
.fire-button.one {
  bottom: 4.5rem;
}
.fire-button.two {
  bottom: 7.5rem;
  width: 5rem;
  grid-template-columns: 1fr 1fr;
}
.fire-button.three {
  bottom: 10.5rem;
  width: 7.5rem;
  grid-template-columns: 1fr 1fr 1fr;
}
.fire-button.four {
  bottom: 13.5rem;
  width: 10rem;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.fire-button.five {
  bottom: 16.5rem;
  width: 12.5rem;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}
.drop-pin.place-list div {
  padding: 0.125rem 0;
  margin: 0.25rem 0;
}
.drop-pin.place-list button {
  cursor: pointer;
  font-size: 1rem;
  display: block;
  width: 12ch;
  padding: .5rem 0.25rem;
  text-align: center;
  margin: 1rem auto;
  background-color: #ffffff;
  border: none;
  border-radius: 0.25rem;
  box-shadow: 1px 1px 3px #666;
}
.drop-pin.place-list h3 {
  font-size: 1rem;
  font-weight: 400;
  margin: 0.25rem;
}
.drop-pin.place-list address {
  font-size: 0.75rem;
  font-weight: 300;
  font-style: italic;
  margin: 0.25rem;
}
.drop-pin.place-list hr {
  margin: 1.25rem 0 0.75rem 0;
}
.drop-pin.place-list .select-dropdown-custom-place label {
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1rem;
}
.drop-pin.place-list .select-dropdown-custom-place input {
  outline: none;
  border: none;
  font-size: 1rem;
  background-color: transparent;
}
.drop-pin.place-list .select-dropdown-custom-place img {
  width: 100%;
}
.drop-pin.place-list header {
  text-align: center;
  max-width: 75vw;
  margin: auto;
  text-decoration: underline;
}
.drop-pin.place-list h1 {
  color: var(--color-primary-200);
}
@media (hover: hover) {
  .fire-button:hover {
    box-shadow: 2px 2px 7px gray;
  }
  .drop-pin.place:hover {
    background-color: var(--color-primary-300);
    box-shadow: 2px 2px 5px var(--color-primary-300-4);
  }
  .drop-pin.place-list .exit:hover {
    box-shadow: 2px 2px 5px #666;
  }
  .drop-pin.place-list .select-dropdown div:hover {
    text-decoration: underline;
    cursor: pointer;
  }
}
@-webkit-keyframes ol-map-fade-in {
  0% {
    opacity: 0;
  }
}
@keyframes ol-map-fade-in {
  0% {
    opacity: 0;
  }
}
#ol-map {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  -webkit-animation: ol-map-fade-in 2s;
          animation: ol-map-fade-in 2s;
}
#ol-map.hidden {
  visibility: hidden;
}
@-webkit-keyframes auth-button-rise {
  0% {
    bottom: 0;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes auth-button-rise {
  0% {
    bottom: 0;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.auth-button {
  --position: 2;
  --bkg: #fff;
}
.auth {
  display: grid;
  justify-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}
.auth hr {
  width: 100%;
}
.auth header {
  text-align: center;
}
.auth .error {
  color: var(--color-secondary-500);
}
.auth input {
  outline: none;
  border: none;
  background-color: transparent;
  border-bottom: 1px dashed var(--color-accent-200);
  max-width: 16.5ch;
  padding: 0.5rem 2ch;
  line-height: 0.9rem;
  font-size: 0.9rem;
}
.auth button {
  border: 1px solid var(--color-primary-200);
  border-radius: 0.25rem;
  display: inline-block;
  margin: 1.5rem auto;
  padding: 0.5rem 2ch;
  background-color: transparent;
}
.profile header {
  display: grid;
  text-align: center;
  align-items: center;
  grid-template-columns: 1rem 1fr 1rem;
}
.profile .clickable {
  cursor: pointer;
}
.profile section {
  display: grid;
  grid-template-columns: 1fr 1rem;
  grid-template-areas: "label img"
                       "value img";
  grid-gap: 1rem;
  gap: 1rem;
  max-width: 40ch;
  align-items: center;
  margin: 2rem auto;
}
.profile section label {
  grid-area: label;
}
.profile section input, .profile section p {
  grid-area: value;
  margin: 0;
}
.profile section span {
  grid-area: img;
}
.profile input {
  outline: none;
  border: none;
  border-bottom: 1px dashed var(--color-primary-200);
  background: transparent;
}

